.card {
    position: relative;
    border: 1px solid #F3F3F3;
    padding: 30px;
    width: 210px;
    border-radius: 40px;
    display: flex;
    flex-direction: column;
    margin-right: 20px;
    margin-left: 20px;
    margin-bottom: 20px;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
    
    &:hover {
      box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.06);
      transform: translateY(-5px);

    }
    .plus {
        cursor: pointer;
    }
    p {
        font-size: 14px;
        line-height: 17px;
        color: #000000;
        margin-bottom: 14px;
    }
    &__info {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__price {
        display: flex;
        flex-direction: column;
        justify-content: center;
        span {
            display: block;
            font-size: 11px;
            line-height: 13px;
            text-transform: uppercase;
            color: #BDBDBD;
            margin-bottom: 2px;
        }
        p {
            font-weight: bold;
            font-size: 14px;
            line-height: 17px;
        }

    }
    
}